/* board_members_page_header */

#admin_board_members_page_header_mobile{
	display: none;
}

#admin_board_members_page_main_mobile{
	display: none;
}


#admin_board_members_page_header{
	height: 13vh;
	width: 100%;
	position: sticky;
	top: 0;
	left: 0;
	background-color: rgba(0, 0, 0,0.95);
	color: var(--fourth-color);
	z-index: 3;
	box-sizing: border-box;
}

#admin_board_members_page_header_nav{
	height: 100%;
	width: 100%;
	display: flex;
	align-items: center;
	justify-content: space-between;
}

#admin_board_members_page_header_nav_img{
	width: 175px;
	height: 80%;
	border: solid white 1.5px;
	margin-left: 2%;

}

#admin_board_members_page_header_nav_h1{
	margin-right: 10%;
}

#admin_board_members_page_header_nav_a2{
	text-decoration: none;
	color: var(--fourth-color);
	padding: 1%;
	padding-left: 2%;
	padding-right: 2%;
	margin-right: 2%;
	border: solid var(--fourth-color) 2px;
}
#admin_board_members_page_header_nav_a2:hover{
	background-color: var(--fourth-color);
	color: black;
}

/* admin_board_members_page_main */

#admin_board_members_page_main{
	width: 100%;
	height: 110vh;
	padding-top: 3%;
	background-color: var(--third-color);
	color: var(--fourth-color);
	display: flex;
	flex-direction: column;
	align-items: center;
	overflow-y: scroll;
	padding-bottom: 5%;

}


#admin_board_members_page_main_h2{
	background-color: var(--fourth-color);
	color: var(--third-color);
	padding: 1%;
	width: 80%;
	text-align: center;

}

#admin_board_members_page_main_div_loading{
	
}

#admin_board_members_page_main_h21{
	background-color: var(--fourth-color);
	color: var(--third-color);
	padding: 1%;
		width: 80%;
	text-align: center;
}

#admin_board_members_page_main_div_loading{

}

#admin_board_members_page_main_section{
	margin-top: 2%;
	width: 95%;
	display: flex;
	align-items: flex-start;
	flex-direction: column;
	justify-content: flex-start;
	box-sizing: border-box;

}

#admin_board_members_page_main_section_nav{

	background-color: rgba(0, 0, 0, 0.8);
	width: 100%;
	display: flex;
	justify-content: flex-end;
	align-items: center;
	flex-direction: row;
	padding: 0;
	box-sizing: border-box;
}



.admin_board_members_page_main_section_nav_div_h5{

	display: flex;
	justify-content: center;
	align-items: center;
	padding: 1%;
	font-size: 16px;
	cursor: pointer;
	border-bottom: var(--fourth-color) solid 2px;
	margin: 0;
	white-space: nowrap;
	box-sizing: border-box;
}
.admin_board_members_page_main_section_nav_div_h5:hover{
	background-color: white;
	color: var(--third-color);

}

#admin_board_members_page_main_section_nav_div_h51{
	cursor: default;
	margin-right: 2%;
	background-color: var(--main-color);
	color: var(--fourth-color);
	padding-left: 2%;
	padding-right: 2%;
	padding: 1%;
	border-bottom: none;
}




.admin_board_members_page_main_section_nav_div_h5_selected{
	background-color: white;
	color: var(--third-color);
}

#admin_board_members_page_main_section_ul1{
	display: flex;
	flex-direction: column;
	flex-wrap: wrap;
	width: 90%;
	box-sizing: border-box;
	height: 60vh;
	overflow-x: scroll;
	overflow-y: hidden;

}
#admin_board_members_page_main_section_ul1::-webkit-scrollbar {
  width: 10px;
}
/* scroll bar thumb (the actual scroll) */
#admin_board_members_page_main_section_ul1::-webkit-scrollbar-thumb {
  background: black /* scrollbar color */
}
/* scrroll bar track */
#admin_board_members_page_main_section_ul1::-webkit-scrollbar-track {
  background: var(--fourth-color); /* track color */
}

.admin_board_members_page_main_section_ul1_li{
	list-style: none;
	margin:3%;
	margin-right: 5%;
}


.admin_board_members_page_main_section_ul1_li_a{
	height: 100%;
	width: 100%;
	text-decoration: none;
	font-size: 16px;
	cursor: default;
	display: flex;
	flex-direction: row;
	justify-content: flex-start;
	align-items: center;
}

.admin_board_members_page_main_section_ul1_li_a_span1{
	cursor: pointer;
	background-color: var(--fourth-color);
	color: var(--third-color);
	padding: 1%;
	white-space: nowrap;
	height: 100%;
	display: flex;
	justify-content: flex-start;
	align-items: center;
	border-top-left-radius: 5px;
	border-bottom-left-radius: 5px;
	border-right: solid 4px grey;
	padding-left: 3%;
}
.admin_board_members_page_main_section_ul1_li_a_span1:hover{
	background-color: white;
	color: black;
	border-right: solid 4px black;
}

.admin_board_members_page_main_section_ul1_li_a_span2{
	background-color: rgb(64, 68, 106);
	color: white;
	padding: 1%;
	white-space: nowrap;
	height: 100%;
	display: flex;
	justify-content: flex-start;
	align-items: center;
	border-top-right-radius: 5px;
	border-bottom-right-radius: 5px;
	padding-right: 3%;
}

